	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"The Berlin Wahl","date":"21 Oct 2001","slug":"/2001-10-21-the-berlin-wahl/","tags":"Y2001"},"html":"<p>Election Day in Berlin today, and the chance to use a very bad bilingual pun in my headline. \";->\"</p>"},"next":{"fields":{"title":"Hectic","date":"20 Oct 2001","slug":"/2001-10-20-hectic/"}},"previous":{"fields":{"title":"Can You Pronounce Wowereit?","date":"22 Oct 2001","slug":"/2001-10-22-can-you-pronounce-wowereit/"}}},{"node":{"fields":{"title":"Hectic","date":"20 Oct 2001","slug":"/2001-10-20-hectic/","tags":"Y2001"},"html":"<p>After being out two days with stomach flu, and a hectic Friday, we're back online. Then again, every day with Christopher is hectic, now isn't it? Especially when his back molars are pushing through.</p>\n<p>Heard yesterday on <a href=\"http://www.ndr4.de\">NDR4</a>: there have been over 600 Anthrax hoaxes in Germany the past week. 10% of all postal mail has been delayed as a result.</p>\n<p>Sunday <a href=\"http://hal.editthispage.com/2001/10/20\">could be the day</a> for Hal and Audrey, quite a bit earlier than planned. But don't births usually <i>not</i> go as planned? Expect the unexpected!</p>\n<p>We watched MSNBC on satellite for a bit this evening, just to get an idea of the tone of reporting in the US (which we can't get from CNN International because it is too, well, international). My main impression was this: is the American media really reduced to using <i>toy</i> helicopters as props to report on Afghanistan?</p>\n<p>I was boycotting news about <b>FC St. Pauli</b> until there was something good to report. In the 10th match of the season, they finally got a win, <a href=\"http://www.bundesliga.de/en/bundesliga/2001/10/01904/nachbericht.shtml?\">4-0 at home against Cottbus</a>. They now have 7 points in 10 games, and are next-to-last instead of dead last.</p>"},"next":{"fields":{"title":"GI Virus","date":"17 Oct 2001","slug":"/2001-10-17-gi-virus/"}},"previous":{"fields":{"title":"The Berlin Wahl","date":"21 Oct 2001","slug":"/2001-10-21-the-berlin-wahl/"}}},{"node":{"fields":{"title":"GI Virus","date":"17 Oct 2001","slug":"/2001-10-17-gi-virus/","tags":"Y2001"},"html":"<p>Christopher was kind enough to pass on his gastro-intestinal flu to me last evening. I'm home sick from work, and wondering just what horse kicked me in the stomach. <b>Later</b> I slept most of the day, but don't feel much better. </p>\n<p>Reuters: <a href=\"http://biz.yahoo.com/rf/011017/n17232004_1.html\">MCD restructures U.S. operations</a>.</p>"},"next":{"fields":{"title":"No Rest for the Wicked","date":"16 Oct 2001","slug":"/2001-10-16-no-rest-for-the-wicked/"}},"previous":{"fields":{"title":"Hectic","date":"20 Oct 2001","slug":"/2001-10-20-hectic/"}}}],"pathPrefix":"","first":false,"last":false,"index":967,"pageCount":1101}}

